import React from 'react';
import { style } from 'typestyle';

const wrapperStyle = style({
    display: ["-webkit-box", "-moz-box", "-ms-flexbox", "-webkit-flex", "flex"],
    display: "-ms-flex",
    display: "-webkit-flex",
    flexDirection: "row",
    justifyContent: "flex-start",
    alignContent: "flex-start",
    $nest:{
        "div":{
            textAlign: "left!important",
            flexGrow: "1",
            flexBasis: "0",
            padding: "20px"
        }
    }
});


export default ()=>{
    return(
        <div className={`pageSection ${wrapperStyle}`}>
            <div>
            <h1>O nama</h1>
                <p> Firma Pinomont doo je generalni uvoznik i distributer Areon kozmetike za automobile na teritoriji Crne Gore. 
                    I pored velike krize koja je zavladala na globalnom nivou, naa firma je imala jasno zacrtan cilj tako da je strateki planiranim koracima, iz dana u dan biljeila
                    uspjehe na tritu. <br />
                    Sjedite firme je u Beranama u ul. Miljana Tomiia. Osniva i vlasnik firme je Aleksandar Balevi, koji je svojim portvovanim pristupom, uspio odrati i 
                    dovesti je na nivo na kojem je ona danas. <br/>
                    irok asortiman proizvoda, niske cijene, brzina i efikasnost su aspekti na kojima je stavljen akcenat poslovanja nae firme. 
                </p>
            </div>
            <div>Galerija</div>
        </div>
    );
}


